.nav {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.nav__wrapper {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 15;
  cursor: pointer;
  height: 100%;
  width: 100%;
}

.nav__title {
  font-size: 1rem;
  color: #fff;
  font-family: "Montserrat";
}

.nav__title.active {
  color: #000000;
}

.nav__button {
  position: relative;
  margin-right: 8px;
}

.nav__button.active {
  background-color: #000000;
}

.nav__button.active::before, .nav__button.active::after {
  background-color: #000000;
}

.nav__button::before, .nav__button::after {
  content: "";
  display: block;
  position: absolute;
  transition: 0.2s;
}

.nav__button, .nav__button::before, .nav__button::after {
  width: 22px;
  height: 2px;
  background-color: #fff;
  border-radius: 3px;
}

.nav__button::before {
  top: -4px;
}

.nav__button::after {
  top: 4px;
}

.menu {
  position: absolute;
  top: 0;
  right: 0;
  width: 36.1%;
  height: 100vh;
  transform: translate(100%);
  transition: 0.5s;
  background-color: rgba(34, 34, 34, 0.95);
  z-index: 10;
}

.menu__wrapper {
  margin: 180px auto 0;
  text-align: center;
}

.menu__listFrame {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu__listItem > a {
  font-family: "Montserrat";
  font-weight: 800;
  display: block;
  color: #ffffff;
}

.menu__listItem:not(:first-of-type) > a {
  margin-top: 30px;
}

@media (max-width: 959px) {
  .menu {
    width: 65.1%;
  }
}

@media (max-width: 559px) {
  .menu {
    width: 100%;
  }
}

#nav__trigger {
  display: none;
}

#nav__trigger:checked ~ .nav__wrapper > .nav__button {
  background-color: transparent;
}

#nav__trigger:checked ~ .nav__wrapper > .nav__button::before {
  content: "";
  transform: rotate(45deg);
  top: 0;
}

#nav__trigger:checked ~ .nav__wrapper > .nav__button::after {
  content: "";
  transform: rotate(-45deg);
  top: 0;
}

#nav__trigger:checked ~ .menu {
  transform: translate(0);
}

