    z-index: 5;
    bottom: 0;
    width: 178px;
    height: 15px;
    display: flex !important;
    align-items: center;
    transform-origin: right bottom;
    transform: rotate(90deg);
    justify-content: space-between;
    right: 97%;
  }
  .mainVisual__scroll::before {
    content: "";
    display: block;
    width: 118px;
    height: 1px;
    background-color: #000;
  }
  .mainVisual__scroll::after {
    content: "SCROLL";
    display: block;
    transform: rotate(180deg);
    font-family: "Montserrat";
    font-weight: 800;
    color: #333333;
    font-size: 12px;
  }
}

.form {
  padding: 0 1px;
}

.form__wrapper {
  margin-top: 120px;
}

.form__box {
  margin-top: 40px;
}

.form__box--deco {
  text-align: center;
}

.form__description {
  font-family: "Noto Sans JP";
  font-weight: 400;
  color: #333333;
  line-height: 1.5;
}

.form__description--deco {
  border: 1px solid #000000;
  height: 252px;
  overflow-y: scroll;
  padding: 10px;
  margin-top: 8px;
}

.form__partition {
  display: flex;
}

.form__list {
  display: flex;
  align-items: center;
  margin-top: 18px;
}

.form__inputCheck {
  vertical-align: bottom;
}

.form__inputCheck--deco {
  width: 20px;
  height: 20px;
  position: relative;
  top: 1px;
  margin: 0;
}

.form__label {
  margin-left: 10px;
}

.form input[type="text"] {
  height: 40px;
}

.form input[type="radio"] {
  width: 20px;
  height: 20px;
  position: relative;
  bottom: 2px;
}

.form input[type="text"],
.form textarea {
  width: 100%;
  margin-top: 8px;
}

.form textarea {
  height: 212px;
}

.form__title {
  font-family: "Noto Sans JP";
  font-weight: 400;
  color: #333333;
}

.form__title--first {
  margin-top: 56px;
}

.form__title--deco::after {
  content: "必須";
  padding: 5px 4px;
  background-color: #222222;
  color: #ffffff;
  display: inline-block;
  font-size: 12px;
  vertical-align: text-top;
  margin-left: 12px;
}

.form__title--privacy {
  font-family: "Montserrat";
  font-weight: 800;
  color: #333333;
}

.form__approve {
  letter-spacing: -0.04em;
}

.form .BUTTON {
  margin: 40px auto 0px;
}

@media (max-width: 959px) {
  .form__wrapper {
    margin-top: 135px;
  }
}

@media (max-width: 559px) {
  .form .BUTTON {
    margin-top: 62px;
  }
  .form__wrapper {
    margin-top: 80px;
  }
  .form__box--deco {
    text-align: initial;
  }
  .form__partition {
    flex-direction: column;
  }
  .form__inputCheck--deco {
    width: 16px;
    height: 16px;
    top: -1px;
  }
}
